<template>
	<div class="participate">
		<div class="participate_header">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="participate_title">我的参与</p>
		</div>
		<p class="openType">
			<span :class="nowOpenType == item.id?'act':''" v-for="item in openTypeList" :key="item.id" @click="selectOpenType(item)">{{ item.text }}</span>
		</p>
		<ul class="goods_list">
			<li @click="tofoodsDetails(item)" v-for="(item,index) in goodsList" :key="index">
				<img :src="item.commodityMainImg" alt="" class="img_goods">
				<div class="goods_list_box">
					<p class="text1">
						<img :src="item.logo" alt="" class="icon_logo">
						<span>{{ item.commodityName }}</span>
					</p>
					<p class="text2">
						<i class="icon_time"></i>
						<span>开奖时间：{{ item.endTimeStr }}</span>
					</p>
					<p class="text3">
						<i class="icon_gift"></i>
						<span>共{{ item.quantity }}份奖品</span>
						<i class="icon_heat"></i>
						<span>已参与{{ item.totalCount }}次</span>
					</p>
					<p class="text4">
						<span class="span1">0元 | 包邮</span>
						<span class="span2">{{ '￥ '+item.officialPrice }}</span>
					</p>
				</div>
				<p class="goods_list_stage">第{{ item.cycle }}期</p>
				<p class="goods_list_btn"></p>
				<img src="../../static/img/img_wzj.png" class="img_wzj" v-if="winPrizeType == 'already' && !item.status"/>
				<img src="../../static/img/img_zjl.png" class="img_zjl" v-if="winPrizeType == 'already' && item.status"/>
			</li>
		</ul>
		<p class="no_participate" v-if="goodsList.length == 0" @click="toPath('/freeAccess')"></p>
		<div v-show="adLoadingFlag" class="shadow">
			<van-loading size="36px" vertical text-color="#ffffff" color="#ffffff">加载中...</van-loading>
		</div>
	</div>
</template>

<script>
	export default{
		name:'participate',
		data(){
			return{
				adLoadingFlag:false,
				winPrizeType:'stay',
				nowOpenType:0,
				openTypeList:[
					{
						id:0,
						text:'待开奖',
						value:'stay',
					},
					{
						id:1,
						text:'已开奖',
						value:'already'
					}
				],
				goodsList:[],
				obj:{}
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.participantRecord();
		},
		methods:{
			toPath(path){
				this.$router.push({path:path});
			},
			goBack(){
				this.$router.go(-1);
			},
			selectOpenType(item){
				this.nowOpenType = item.id;
				this.winPrizeType = item.value;
				this.goodsList = [];
				this.participantRecord();
			},
			participantRecord(){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					winPrizeType:_this.winPrizeType
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/prize/goods/participantRecord',
					data:redata,
				}).then(function(res){
					console.log(res);
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						for(var i in reData){
							reData[i].endTimeStr = _this.$timestampTpDate(reData[i].endTime,'ymdhms');
						}
						_this.goodsList = reData;
					}
					_this.adLoadingFlag = false;
				})
			},
			tofoodsDetails(item){
				var itemObj = JSON.stringify(item);
				localStorage.setItem("itemObj",itemObj);
				this.$router.push({path:'/freeAccess/foodsDetails'});
			}
		}
	}
</script>

<style scoped>
	.participate{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fcebfe;
		padding-bottom:0.8rem;
	}
	.participate_header{
		display: flex;
		align-items:center;
		width: 10.8rem;
		height: 2.32rem;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.3rem;
		padding-right:0.3rem;
		position: relative;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
	}
	.icon_arrow_left{
	  display: inline-block;
	  width: 0.56rem;
	  height:0.56rem;
	  background-image: url(../../static/img/icon_arrow_left.png);
