		justify-content: center;
		margin-top:0.4rem;
	}
	.openType span{
		display: inline-block;
		margin-right:0.33rem;
		width: 2.48rem;
		height: 0.78rem;
		border-radius: 0.39rem;
		font-size: 0.48rem;
		font-weight: 600;
		color:#ff4780;
		text-align: center;
		line-height: 0.78rem;
	}
	.openType span.act{
		color:#fff;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
	}
	.goods_list{
		width: 100%;
		margin-top:0.38rem;
		box-sizing: border-box;
		padding:0.3rem;
	}
	.goods_list li{
		min-height: 3rem;
		border-radius: 0.3rem;
		background:#fff url(../../static/img/bg_goodslist.png) no-repeat;
